<template>
    <div class="mt-16">
        <div>
            <div class="flex items-end">
            <svg class="fill-current h-5 text-red mb-1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M1 10h3v10H1V10zM6 0h3v20H6V0zm5 8h3v12h-3V8zm5-4h3v16h-3V4z"/></svg>
            <div class="ml-4">
                <h2 class="text-2xl text-purple-lightest">Related Gifs</h2>
            </div>
            </div>
            <div class="w-full h-px -mt-1" style="background: linear-gradient(to right, red, violet, transparent 55%)"></div>
        </div>
        <div class="flex flex-wrap mt-6">
            <div :key="gif.id" v-for="gif in gifs" class="flex-no-shrink w-1/5 relative p-2">
                <gif :gif="gif"></gif>
            </div>
        </div>
    </div>
</template>
<script>
/* eslint-disable */
import config from '../../config'
import Link from './Link'

export default {
    name: 'Related',

    components: {
        'gif': Link
    },

    props: {
        gifTitle: String 
    },

    watch: {
        gifTitle() {
            this.displayResult();
        }
    },

    data() {
        return {
            gifs: []
        }
    },

    created() {
        this.displayResult();
    },

    methods: {
        displayResult() {
            axios.get(config.url('search', `q=${this.gifTitle}`))
            .then(({data}) => {
                this.gifs = data.data;
            })
            .catch(function (error) {
                console.log(error);
            })
        }
    }
}
</script>