import Home from './components/Home'
import About from './components/About'
import SearchResult from './components/SearchResult'
import GifShow from './components/gif/Show'

export const routes = [
    { 
        path: '/giphy-api-by-rick/', 
        name: 'home',
        component: Home 
    },
    { 
        path: '/giphy-api-by-rick/about',
        name: 'about', 
        component: About 
    },
    { 
        path: '/giphy-api-by-rick/search-result',
        name: 'search-result', 
        component: SearchResult 
    },
    { 
        path: '/giphy-api-by-rick/gifs/:id',
        name: 'gif-show', 
        component: GifShow 
    },
]