<template>
    <div class="w-1/4 px-6">
      <div>
        <div class="flex items-end">
          <svg class="fill-current flex-no-shrink h-5 text-yellow  mb-3" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z"/></svg>
          <div class="ml-4">
            <span class="text-purple-lighter text-xs tracking-wide font-medium uppercase">Overall</span>
            <h2 class="text-5xl text-purple-lightest">Randomsz</h2>
          </div>
        </div>
        <div class="w-full h-px -mt-3" style="background: linear-gradient(to right, yellow, orange, transparent)"></div>
        <div class="mt-8 rounded-lg">
            <div class="w-full mb-3" :key="gif.id"  v-for="gif in gifs">
                <gif :gif="gif"></gif>
            </div>
        </div>
      </div>
    </div>
</template>
<script>
/* eslint-disable */
import config from '../../config'
import Link from '../gif/Link'

export default {
    name: 'Sidebar Random GIFs',

    components: {
        'gif': Link
    },

    data() {
        return {
            gifs: []
        }
    },

    created() {
        this.displayRandom();
    },

    methods: {
        displayRandom() {
            let MAXIMUM_RANDOM_GIF = 3;
            for(let i=1; i<=MAXIMUM_RANDOM_GIF; i++) {
                axios.get(config.url('random'))
                .then(({data}) => {
                    this.gifs.push(data.data);
                })
                .catch(function (error) {
                    error;
                })
            }
        },
    }
}
</script>
