<template>
    <div>
        <div>
            <div class="flex items-end">
                <svg class="fill-current h-5 text-red  mb-3" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M1 10h3v10H1V10zM6 0h3v20H6V0zm5 8h3v12h-3V8zm5-4h3v16h-3V4z"/></svg>
                <div class="ml-4">
                    <span class="text-purple-lighter text-xs tracking-wide font-medium uppercase">Search Result for:</span>
                    <h2 class="text-5xl text-purple-lightest">{{ keyword }}</h2>
                </div>
            </div>
            <div class="w-full h-px -mt-3" style="background: linear-gradient(to right, red, violet, transparent 35%)"></div>
        </div>    
        <div class="flex flex-wrap mt-6">
            <div :key="gif.id" v-for="gif in gifs" class="flex-no-shrink w-1/5 relative p-2">
                <gif :gif="gif"></gif>
            </div>
            <button class="mx-auto block mt-6 bg-red py-4 px-32 rounded uppercase tracking-wide text-xs font-bold text-white" style="background: linear-gradient(to right, violet, red)" @click="loadMore">load more</button>
        </div>
    </div>    
</template>

<style scoped>
  .gif-container {
    display: inline-block;
    width: 250px;
  }

  .gif-container img {
    width: 100%;
  }
</style>

<script>
/*  eslint-disable */
import config from '../config'
import Gif from './gif/Link'

export default {
    name: 'search-result',

    components: {
        'gif': Gif
    },

    data() {
        return {
            gifs: []
        }
    },

    computed: {
        keyword() {
            return this.$route.query.q;
        }
    },
    
    watch: {
        '$route' (to, from) {
            this.displayResult();
            console.log('worked')
        }
    },

    created() {
        this.displayResult();
    },

    methods: {
        displayResult() {
            axios.get(config.url('search', `q=${this.keyword}`))
            .then(({data}) => {
                this.gifs = data.data;
            })
            .catch(function (error) {
                console.log(error);
            })
        },

        loadMore() {
            let offset = this.gifs.length;
            axios.get(config.url('search', `q=${this.keyword}&offset=${offset}`))
            .then(({data}) => {
                this.gifs = this.gifs.concat(data.data);
            })
            .catch(function (error) {
                console.log(error);
            })
            }        
    }
}
</script>