<template>
    <div class="pass">
        <div class="flex -mx-6">
            <div class="w-1/4 px-6">
                <a v-if="gif.user" :href="gif.user.profile_url" target="_blank" class="no-underline">
                    <div class="rounded-tl-lg rounded-tr-lg p-2 w-full">
                        <div class="flex justify-center p-2">
                            <div class="rounded-full inline-flex items-center justify-center p-1 shadow-lg" style="background: linear-gradient(to top, red, violet 25%, orange)">
                                <img class="w-32 h-32 rounded-full" :src="gif.user.avatar_url" alt="">
                            </div>
                        </div>
                        <div class="flex flex-col items-center">
                            <div class="flex items-center mt-4">
                                <h3 class="font-medium text-base text-center text-purple-lightest">
                                    <span>@{{ gif.username }}</span>
                                </h3>
                                <div v-if="gif.user.is_verified" class="flex ml-2 flex-no-shrink w-4 h-4 rounded-full items-center justify-center" style="background: linear-gradient(to top, red, orange)">
                                    <svg class="h-2 fill-current text-white" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M0 11l2-2 5 5L18 3l2 2L7 18z"/></svg>    
                                </div> 
                            </div>
                            <div class="mt-4 text-center">
                                <p class="text-purple-lighter tracking-wide text-xs leading-loose">Please visit the profile to view the other gifs of this author.</p>
                            </div>
                        </div>
                    </div>
                </a>
                <div class="flex flex-col items-center" v-else>
                    <div class="flex items-center mt-4">
                        <h3 class="font-medium text-base text-center text-purple-lightest">
                            <span>No user for this GIF</span>
                        </h3>
                    </div>
                    <div class="mt-4 text-center">
                        <p class="text-purple-lighter tracking-wide text-xs leading-loose">The GIF might be posted from another platforms.</p>
                    </div>
                </div>
            </div>
            <div class="w-3/4 px-6">
                <div>
                    <div class="flex items-end">
                        <svg class="fill-current h-5 text-red  mb-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M10 3.22l-.61-.6a5.5 5.5 0 0 0-7.78 7.77L10 18.78l8.39-8.4a5.5 5.5 0 0 0-7.78-7.77l-.61.61z"/></svg>
                        <div class="ml-4">
                            <span class="text-purple-lighter text-xs tracking-wide font-medium uppercase">GIF</span>
                            <h1 class="text-3xl uppercase text-purple-lightest">{{ gif.title }}</h1>
                        </div>
                    </div>
                    <!-- <div class="w-full h-px -mt-1" style="background: linear-gradient(to right, red, violet, transparent)"></div> -->
                </div>
                <div class="w-full h-96 mt-4 shadow-lg flex items-center justify-center bg-purple-black">
                    <img class="gif-show__main-image max-h-full h-full max-w-full" v-if="gif.images" :src="gif.images.downsized_large.url" alt="">
                </div>
                <related-gifs :gifTitle="gif.title"></related-gifs>
            </div>
        </div>
    </div>
</template>
<script>
/*  eslint-disable */
import config from '../../config'
import RelatedGifs from './Related'

export default {
    name: 'GIFDetails',

    components: {
        'related-gifs': RelatedGifs
    },

    data() {
        return {
            gif: {}
        }
    },

    watch: {
        $route() {
            this.getGif();
            let image = document.querySelector('.gif-show__main-image');
            console.log(image);
        }
    },

    created() {
        this.getGif();
    },

    methods: {
        getGif() {
            axios.get(config.url(this.$route.params.id))
            .then(({data}) => {
                this.gif = data.data;
            })
            .catch(function (error) {
                console.log(error);
            })
        }
    }
}
</script>